html{
    line-height: 1.15rem;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    background: linear-gradient(190deg,#867bcb );
    background: #867bcb;
    background-repeat: no-repeat;
    background-size: cover;
    color: #222;
    overflow: hidden;
    background-attachment: fixed;
}

.App{
    margin-top: 3rem;
    display: flex;
    flex-direction: column;

}
.App h1{
    display: inline;
    text-align: center;
    margin-bottom: 2rem;
    color: #e1ebfd;
    text-shadow: 0 0 5px #433aa8,3px -1px;
}

.addTodos{
    display: flex;
    justify-content: center;
}
.todo-input{
    min-width: 15rem;
    width: 40vw;
    max-height: 2.5rem;
    border: none;
    padding: 0.5rem 1rem;
    align-self: center;
    background-color: #e1ebfd;
}

.todo-input:focus{
    outline: none;
    border: 2px solid #433aa8;
}

.add-btn{
    margin-left: 1rem;
    background-color: #271c6c;
    border-radius: 50%;
    border: 2px solid #e1ebfd;
    font-size: 1.5rem;
    color: #e1ebfd;
    cursor: pointer;
    height: 3.2rem;
    width: 3.2rem;
    box-shadow: 2px 4px 10px #271c6c;
    display: flex;
    justify-content: center;
    align-items: center;


}
.add-btn:focus{
    outline: none;
}
.displaytodos{
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.displaytodos .buttons{
    margin-bottom: 2px;
}
.displaytodos .buttons button{
    padding: 0.5rem 1.2rem;
    border-radius: 20px;
    cursor: pointer;
    border: none;
}

.displaytodos .buttons button:focus{
    outline: none;
}
.displaytodos .buttons button:not(:last-child){
    margin-right: 1rem;
}

.displaytodos ul{
    list-style: none;
    display: flex;
    align-self: flex-start;
    flex-wrap: wrap;
    margin-left: 5%;
    margin-top: 5%;
}

.card{
    display: flex;
    flex-direction: column;
    background: rgb(180,182,218);
    background: radial-gradient(circle,
    hsla(237,37%,0.8,0.9)0%,
    hsla(219,88%,94%,0.9)100%,
    
    );
    margin: 0 1rem 1rem 0;
    height: 8rem;
    width: 18rem;
    border-radius: 0.5rem;
    padding: 1rem;
    position: relative;
}

.card textarea{
    padding: 0.5rem;
    border-radius: 8px;
    border: none;
    background-color: #e1ebfd;
    color: #271c6c;
}

.card textarea:disabled{
    background-color: transparent;
}

.card .btns{
    position: absolute;
    bottom: 1rem;
    right: 1rem;
}

.card .btns button{
    border-radius: 20%;
    border: none;
    margin: 0 0.6rem;
    font-size: 1.4rem;
    cursor: pointer;
    color: #271c6c;
    background-color: transparent;
}

.card .btns button:focus{
    outline: none;
}


.completed{
    position: absolute;
    right: 0.3rem;
    top: 0.3rem;
    background-color: #867bcb;
    border: 2px solid #271c6c;
    color: #e1ebfd;
    font-size: 0.8rem;
    padding: 0.3rem 1rem;
    border-radius: 20px;
}